*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}
hr{
    height: 1px;
    background: #929292;
    border: none;
}
.app{
    height: 100vh;
    background: url('../../imgs/bg.jpg') center/cover;
}
.app-container{
    max-width: 300px;
    min-height: 400px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 15px;
    border-radius: 5px;
    border: 1px solid #000;
    backdrop-filter: blur(10px);
}
.sun{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
}
.sun__img{
    width: 80%;
    margin-bottom: 31px;
}
