.weather__main-info{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}
.weather__city{
    font-weight: 700;
    font-size: 25px;
    font-style: normal;
}
.weather__description{
    text-transform: capitalize;
}
.weather__temp{
    display: flex;
    position: relative;
}
.weather__deg{
    font-size: 28px;
    margin-top: 10px;
}
.sunRiseSet{
    margin-top: 10px;
    display: grid ;
    grid-template-columns: [start] 50% 50% [end];
    padding:  10px 0;
}
.wind-feel{
    display: grid;
    grid-template-columns: [start] 50% 50% [end];
    padding: 10px 0;
}
.humidity-pressure{
    display: grid ;
    grid-template-columns: [start] 50% 50% [end];
    padding: 10px 0;
}
.sunTime, .windTemp, .airInfo{
    font-size: 20px;
    font-weight: 600;
}
