.search-form{
    width: 100%;
    box-shadow: 0 0 10px 0 #777777;
    border-radius: 20px;
}
.search-form__input{
    width: 80%;
    padding: 10px;
    border: none;
    background: none;
    border-right: 1px solid #747474;
}
.search-form__input:focus{
    outline: none;
}
.search-form__input::placeholder{
    color: #000;
}
.search-form__btn{
    width: 20%;
    padding: 10px 0;
    border: none;
    background: none;
}
.search-vector{
    width: 40%;
    margin: -8px;
    margin-left: -15px;
}